.back-to-top-btn {
    position: fixed;
    right: 3%;
    bottom: 3%;
    opacity: 0.9;
    padding: 12px;
    border: none;
    border-radius: 100%;
    background: $cl-accent;
    box-shadow: $cl-modal-text 0px 3px 8px;
    cursor: pointer;
    transition: opacity 250ms cubic-bezier(.4, 0, .2, 1), transform 300ms cubic-bezier(.4, 0, .2, 1);

    &:hover,
    &:focus {  
        box-shadow: $cl-modal-text 0px 5px 15px;
        opacity: 1;
    };

    &.is-hidden {
        opacity: 0;
        pointer-events: none;
        transform: translate(50%, 50%) scale(1.2);
    };

    
}
.back-to-top-icon {
    width: 35px;
    height: 35px;
    fill: $cl-white-primary;

    @include tab {
        width: 40px;
        height: 40px;
    }

    @include desc {
        width: 45px;
        height: 45px;
    }
};
.progress-bar {
    width: 0.6%;
    position: fixed;
    top: 0;
    right: 0;
    border-radius: 4px;
    opacity: 0.9;
    background: $cl-accent;
    box-shadow: $cl-modal-text 0px 3px 8px;

    @include tab {
        width: 0.4%;
    }
};
